import React from 'react'
import S from './recoveryStyles'

export default function RecoveryMenu({ playerName, loanAvailable, hasPendingLoan, onGoLoan, onGoReduce, onGoFire, onDeclareBankruptcy }) {
  return (
    <div style={S.body}>
      <p style={S.lead}>
        Voc est sem dinheiro, {playerName}. Escolha uma das opes:
      </p>

      {hasPendingLoan && (
        <div style={{...S.lead, color: '#ef4444', fontWeight: 'bold', marginBottom: '16px'}}>
           Voc j possui um emprstimo pendente. Cada jogador s pode ter um emprstimo por vez.
        </div>
      )}

      <ul style={S.bullets}>
        <li>
          Pedir <b>emprstimo</b> nico com 50% de juros garantido por 50% dos seus <b>BENS</b> (limite {`$ ${loanAvailable}`}).
        </li>
        <li>
          <b>Reduzir</b> nveis de <b>MIX de Produtos</b> ou <b>ERP/Sistemas</b> e receber 50% do valor pago.
        </li>
        <li>
          <b>Demitir</b> funcionrios e receber 50% do valor total.
        </li>
      </ul>

      <div style={S.rowBtns}>
        <button style={{...S.cta, background:'#ef4444'}} onClick={onGoFire}>DEMITIR</button>
        <button style={{...S.cta, background:'#a16207'}} onClick={onGoReduce}>REDUZIR</button>
        <button 
          style={{
            ...S.cta, 
            background: hasPendingLoan ? '#6b7280' : '#16a34a',
            cursor: hasPendingLoan ? 'not-allowed' : 'pointer'
          }} 
          onClick={hasPendingLoan ? undefined : onGoLoan}
          disabled={hasPendingLoan}
        >
          {hasPendingLoan ? 'EMPRSTIMO (J REALIZADO)' : 'EMPRSTIMO'}
        </button>
        <button 
          style={{
            ...S.cta, 
            background:'#d32f2f',
            fontWeight:'bold'
          }} 
          onClick={onDeclareBankruptcy}
        >
          DECLARAR FALNCIA
        </button>
      </div>
    </div>
  )
}
