import React, { useMemo, useState } from 'react'
import S from './recoveryStyles'

/**
 * Props
 * - loanAvailable: number      -> limite calculado (50% dos bens)
 * - alreadyHasLoan?: boolean   -> se true, bloqueia novo emprstimo
 * - onBack(): void
 * - onConfirm(payload): void
 *
 * onConfirm(payload) envia um objeto pronto para o chamador aplicar:
 *   {
 *     type: 'LOAN',
 *     amount: <valor>,
 *     // aplique no jogador: cash += amount
 *     cashDelta: <valor>,
 *     // marque para cobrana na prxima "Despesas Operacionais"
 *     loan: { amount: <valor>, charged: false }
 *   }
 * Caso j exista um emprstimo, envia:
 *   { type:'LOAN_DENIED', reason:'ALREADY_TAKEN' }
 */
export default function RecoveryLoan({
  loanAvailable,
  alreadyHasLoan = false,
  onBack,
  onConfirm
}) {
  const [loanInput, setLoanInput] = useState('')

  const parsed = useMemo(
    () => Math.max(0, Math.floor(Number(loanInput) || 0)),
    [loanInput]
  )
  const canConfirm =
    !alreadyHasLoan && parsed > 0 && parsed <= Number(loanAvailable || 0)

  const confirm = () => {
    // Bloqueia se j houver emprstimo marcado
    if (alreadyHasLoan) {
      onConfirm?.({ type: 'LOAN_DENIED', reason: 'ALREADY_TAKEN' })
      return
    }

    const limit = Math.max(0, Math.floor(Number(loanAvailable) || 0))
    const val = Math.max(0, Math.min(limit, parsed))

    if (val > 0) {
      // Envia um payload autoexplicativo para o modal pai / App aplicar.
      const payload = {
        type: 'LOAN',
        amount: val,
        cashDelta: val, // somar no saldo imediatamente
        loan: {
          amount: val,
          charged: false // ser cobrado na prxima "Despesas Operacionais"
        }
      }
      console.log('[DEBUG] RecoveryLoan enviando payload:', payload)
      onConfirm?.(payload)
    } else {
      onConfirm?.({ type: 'LOAN_DENIED', reason: 'INVALID_AMOUNT' })
    }
  }

  return (
    <div style={S.body}>
      <div style={S.subHeader}>
        <b style={{ fontSize: 20 }}>EMPRSTIMO</b>
      </div>

      <p>
        Voc pode realizar um emprstimo colocando <b>50% dos seus BENS</b> como
        garantia (pago na prxima Despesas Operacionais).
      </p>

      <div style={S.infoRow}>
        <span>Valor disponvel:</span> <b>${Number(loanAvailable || 0)}</b>
      </div>
      <div style={S.infoRow}>
        <span>Valor garantia:</span> <b>${Number(loanAvailable || 0)}</b>
      </div>

      {alreadyHasLoan && (
        <div
          style={{
            ...S.infoRow,
            color: '#fca5a5',
            border: '1px solid rgba(255,0,0,.25)',
            padding: '8px 10px',
            borderRadius: 8,
            background: 'rgba(255,0,0,.06)'
          }}
        >
          Voc j possui um emprstimo ativo. No  possvel pegar outro.
        </div>
      )}

      <input
        style={S.input}
        type="number"
        min={0}
        max={Math.max(0, Number(loanAvailable || 0))}
        placeholder="Digite o valor que quer emprestar"
        value={loanInput}
        onChange={(e) => setLoanInput(e.target.value)}
        disabled={alreadyHasLoan}
      />

      <div style={{ fontSize: 12, opacity: 0.8, marginTop: 6 }}>
        Mx: ${Number(loanAvailable || 0)}  valor ser somado ao seu saldo e
        cobrado na prxima Despesas Operacionais.
      </div>

      <div style={S.rowBtns}>
        <button style={S.back} onClick={onBack}>
           Voltar
        </button>
        <button
          style={{
            ...S.cta,
            background: '#16a34a',
            opacity: canConfirm ? 1 : 0.6,
            cursor: canConfirm ? 'pointer' : 'not-allowed'
          }}
          onClick={confirm}
          disabled={!canConfirm}
        >
          Pegar Emprstimo
        </button>
      </div>
    </div>
  )
}
